@font-face {
  font-family: "Vazirmatn";
  src: url("../../assets/src/fonts/VazirmatnRegular.ttf") format("truetype");
}

* {
  font-family: "Vazirmatn", sans-serif;
  box-sizing: border-box;
  direction: rtl;
}

body {
  margin: 0;
}

p {
  margin: 0 !important;
}

a {
  text-decoration: none;
  color: initial;
  text-align: center;
}

ul {
  padding-inline: 0;
}

ul li {
  list-style-type: none;
}

button {
  display: flex !important;
  align-items: center !important;
}
